<template>
  <el-container class="mg">
    <Header title="管理" />
    <el-button @click="addRole">添加角色</el-button>
    <el-footer></el-footer>
  </el-container>
</template>

<script>
  import {AV} from '../../common/api';
  export default {
    components: {}, computed: {},
    methods: {
      addRole(){
        const roleAcl = new AV.ACL();
        roleAcl.setPublicReadAccess(true);
        roleAcl.setWriteAccess(AV.User.current(), true);
        const admin = new AV.Role('admin', roleAcl);
        admin.getUsers().add(AV.User.current());
        admin.save().then((res) => {
          console.log({res});
        });
      }
    },
    data () {
      return {
        titleArr: ['Wo', 'rld'],

      }
    },
    created () {

    },
    mounted () {

    }
  }
</script>

<style scoped="">

</style>



